import React from 'react';
import {Container} from '@mui/material';
import formatPrice from '../../../utils/formatPrice';
import ButtonCus from '../../../components/ButtonCus';

function PaymentTotal(props:any) {
      const { methodList, value, carts } = props;
      const pmMethod = methodList.find((item:any) => item.method === value)
      return (
            <div className="payment-total">
                  <Container>
                        <div className="flex-row payment-total__head">
                              <span>Nhp m khuyn mi</span>
                              <input type="text" placeholder="M khuyn mi" />
                        </div>
                        <div className="flex-row payment-total__body">
                              <div className="payment-total__body-item">
                                    <p>Phng thc thanh ton</p>
                                    <p>Tng tin</p>
                                    <p>Gim gi</p>
                                    <p>Tng thanh ton</p>
                              </div>
                              <div className="payment-total__body-item">
                                    <p
                                          style={{ color: 'var(--text-black)' }}
                                    >
                                          {pmMethod ? pmMethod?.title : 'Vui lng chn phng thc thanh ton'}
                                    </p>
                                    <p>{formatPrice(carts.cartAmount)} </p>
                                    <p>0 </p>
                                    <p>{formatPrice(carts.cartAmount)} </p>
                              </div>
                        </div>
                        <div className="flex-row-sp payment-total__body-submit">
                              <span>Nhn "Thanh ton"  hon tt</span>
                              <ButtonCus
                                    text="Thanh ton"
                                    color="var(--purple)"
                                    border="solid 1px var(--purple)"
                                    borderRadius="16px"
                              />
                        </div>
                  </Container>
            </div>
      );
}

export default PaymentTotal;